/* Padding and background for the card container */
.cards {
	padding: 4rem;
	background: #fff;
}

/* Center-aligning all h1 elements */
h1 {
	text-align: center;
}

.cards-container {
	display: flex;
	flex-flow: column;
	align-items: center;
	width: 90%;
	margin: 0 auto;
	font-size: 18px;
}

.cards-container h1 {
	font-size: 30px;
	text-align: center;
}

.cards-container p {
	display: flex;
	flex-flow: column;
	align-items: center;
	width: 100%;
	margin-top: 20px;
	margin-bottom: 50px;
}

.cards-wrapper {
	position: relative;
	margin: 50px 0 45px;
}

.cards-items {
	margin-bottom: 24px;
}

.cards-item {
	display: flex;
	flex: 1;
	margin: 0 1rem;
	border-radius: 10px;
}

/* Flexbox layout, shadow effect, and other styles for card links */
.cards-link {
	display: flex;
	flex-flow: column;
	width: 100%;
	box-shadow: 0 6px 20px rgba(56, 125, 255, 0.17);
	-webkit-filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
	filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
	border-radius: 10px;
	overflow: hidden;
	text-decoration: none;
}

.cards-wrap {
	position: relative;
	width: 100%;
	padding-top: 67%;
	overflow: hidden;
}

.fade-img {
	animation-name: fade-img;
	animation-duration: 2s;
}

/* Styles for category label within card wrap */
.cards-wrap::after {
	content: attr(data-category);
	position: absolute;
	bottom: 0;
	margin-left: 10px;
	padding: 6px 8px;
	max-width: calc((100%) - 60px);
	font-size: 12px;
	font-weight: 700;
	color: #fff;
	background-color: #1f98f4;
	box-sizing: border-box;
}

/* Absolute positioning and transition for card images */
.cards-img {
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	display: block;
	width: 100%;
	max-width: 100%;
	height: 100%;
	max-height: 100%;
	object-fit: cover;
	transition: all 0.2s linear;
}

.cards-img:hover {
	transform: scale(1.1);
}

.cards-info {
	padding: 20px 30px 30px;
}

.cards-text {
	color: #252e48;
	font-size: 18px;
	line-height: 24px;
}

/* Adjusting container width for large screens */
@media only screen and (min-width: 1200px) {
	.content-blog-container {
		width: 84%;
	}
}

/* Flex display card wrapper on larger screens */
@media only screen and (min-width: 1024px) {
	.cards-items {
		display: flex;
	}
	.cards-wrapper {
		position: relative;
		margin: 50px 0 45px;
		max-width: 1120px;
	}
}

/* Bottom margin adjustment for smaller screens */
@media only screen and (max-width: 1024px) {
	.cards__item {
		margin-bottom: 2rem;
	}
}
